@charset "UTF-8";
body {
  font-family: "'Noto Sans JP';";
  background: #111;
  color: #fff;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.jigyou-naiyou__contact-layout {
  background: rgb(61, 61, 61);
  padding: 4rem 1.25rem;
}
.jigyou-naiyou__contact-title {
  margin-bottom: 1rem;
  font-style: normal;
  font-weight: 400;
  font-size: 1.5625rem;
  letter-spacing: 0.125rem;
  line-height: 1.44;
  position: relative;
}
.jigyou-naiyou__contact-title > p {
  text-align: center;
}
.jigyou-naiyou__contact-title::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -90%);
  content: "Contact";
  display: block;
  color: rgba(249, 248, 113, 0.27);
  font-family: "Noto Serif JP", serif;
  font-style: normal;
  font-weight: 400;
  font-size: 3.125rem;
  letter-spacing: 0.125rem;
  line-height: 1.44;
  text-align: center;
}
.jigyou-naiyou__contact-message {
  margin-bottom: 2.5rem;
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  letter-spacing: 0.125rem;
  line-height: 1.4375;
}
.jigyou-naiyou__inquiry {
  border: solid 1px #fff;
  text-align: center;
  font-style: normal;
  font-weight: 400;
  font-size: 1.25rem;
  letter-spacing: 0.125rem;
  line-height: 1.45;
}
.jigyou-naiyou__inquiry-layout {
  position: relative;
}
.jigyou-naiyou__inquiry-layout::before {
  content: "";
  display: block;
  padding-top: 14.92%;
}
.jigyou-naiyou__inquiry-layout p {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.jigyou-naiyou__footer-copyright {
  text-align: center;
}
.jigyou-naiyou__footer-list-title img {
  position: relative;
  width: 4.375rem;
}
.jigyou-naiyou__footer-list-title a {
  display: inline-block;
}
.jigyou-naiyou__footer-wrap {
  text-align: center;
}
.jigyou-naiyou__kigyou-rinen-contents-layout {
  margin-bottom: 3rem;
}
.jigyou-naiyou__kigyou-rinen-contents-text-wrap > .jigyou-naiyou__kigyou-rinen-contents-title {
  text-align: center;
  margin-bottom: 1.5rem;
}
.jigyou-naiyou__kigyou-rinen-contents-image {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  margin-bottom: 1.5rem;
}
.jigyou-naiyou__kigyou-rinen-contents-image::before {
  content: "";
  display: block;
  padding-top: 67%;
}
.jigyou-naiyou__kigyou-rinen-contents-image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
}
.jigyou-naiyou__kigyou-rinen-contents-text-wrap {
  margin: 0 1.25rem;
}
.jigyou-naiyou__kigyou-rinen-wrap-top {
  margin: 0 1.25rem;
  margin-bottom: 5rem;
}
.jigyou-naiyou__kigyou-rinen-wrap-top > h3 {
  text-align: center;
  margin-bottom: 1.5rem;
}
.jigyou-naiyou__mv-inner {
  position: relative;
  width: 100vw;
  height: 60vh;
  background-image: url(/images/jigyou-naiyou-mv.png);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
}
.jigyou-naiyou__mv-shadow {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background: rgba(17, 17, 17, 0.2);
}
.jigyou-naiyou__scroll-layout {
  display: block;
  position: fixed;
  bottom: 0;
  right: 0;
  width: 2.8125rem;
  height: 2.8125rem;
  background: rgb(255, 255, 255);
}
.js-drawer-menu {
  display: inline-block;
  height: 100%;
  position: absolute relative;
  top: 0;
  right: 0;
  z-index: 5;
}
.js-drawer-menu-wrap {
  display: inline-block;
  position: relative;
  width: 1.5rem;
  height: 100%;
}
.js-drawer-menu-wrap span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1.5rem;
  height: 2px;
  background: #fff;
  transition: 0.75s;
}
.js-drawer-menu-wrap span::before {
  content: "";
  display: block;
  width: 1.5rem;
  height: 0.125rem;
  background: #fff;
  position: absolute;
  top: 8px;
  transition: 0.75s;
}

.js-drawer-menu-wrap span::after {
  content: "";
  display: block;
  width: 1.5rem;
  height: 0.125rem;
  background: #fff;
  position: absolute;
  top: -8px;
  transition: 0.75s;
}

.js-drawer-menu-wrap__open span {
  background: transparent;
  transition: 0.75s;
}
.js-drawer-menu-wrap__open span::before {
  top: 0;
  transform: rotate(45deg);
  transition: 0.75s;
}
.js-drawer-menu-wrap__open span::after {
  top: 0;
  transform: rotate(-45deg);
  transition: 0.75s;
}
.kigyou-naiyou__header-layout {
  height: 3.125rem;
  padding: 0 1.25rem;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  background: rgb(17, 17, 17);
  display: flex;
  justify-content: space-between;
}
.kigyou-naiyou__header-title {
  display: inline-block;
  position: relative;
  width: 4.375rem;
  z-index: 1;
}
.kigyou-naiyou__header-title::before {
  content: "";
  display: block;
  padding-top: 71.4285%;
}
.kigyou-naiyou__header-title img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.sp-menu__layout {
  position: absolute;
  top: 0;
  left: -100%;
  bottom: 0;
  right: 0;
  display: block;
  width: 100vw;
  height: 100vh;
  background: rgb(17, 17, 17);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.75s;
}
.sp-menu__layout-open {
  left: 0;
  transition: 0.75s;
}
.sp-menu__wrap {
  text-align: center;
}
.sp-menu__list {
  margin-bottom: 2rem;
}